      h2 1.1.  Objetivo y procedimiento de verificacin
    p(data-aos="zoom-in") El objetivo principal de la verificacin contable es poder identificar errores materiales y de operacin que puedan afectar los estados financieros del periodo o vigencia de en la que se est trabajando (que corre desde el 1 de enero al 31 de diciembre o lo corrido). 
