        p.mb-0.pe-5.py-2.ps-2 De acuerdo con ello es ideal tener claridad que el trabajo a realizar respecto a los saldos iniciales es obtener evidencia suficiente si se contienen errores que afectan materialmente los estados financieros del perodo y si se han aplicado las normas y polticas contables o si tal vez han existido cambios en los saldos.

    .row.justify-content-center.align-items-center.mt-5          
      .col-lg-10
        p(data-aos="zoom-in") Otro aspecto de la verificacin consiste en determinar si los saldos iniciales del periodo (o finales del periodo anterior), se han pasado correctamente al periodo vigente por ende el procedimiento que debe adelantarse en este proceso parte de la inspeccin de los documentos contables y del registro de informacin en las cuentas contables del negocio:   
    .row.justify-content-center.align-items-center.mt-5          
