          .tarjeta                
            .row.row.justify-content-center.align-items-center
              .col-lg-10.slier-color.px-5.py-4
                h5.my-2 Verificacin hechos
                p Seguidamente se proceder a la revisin, verificacin y consecuencia de la informacin de los hechos econmicos registrados y soportados documentalmente para cada una de las cuentas contables. Como, por ejemplo: caja y bancos efectivo y equivalentes de efectivo.
          .tarjeta                   
            .row.row.justify-content-center.align-items-center
              .col-lg-10.slier-color.px-5.py-4
                h5.my-2 Revisin movimientos
                p Se revisa que los movimientos estn soportados, que se cuente con las conciliaciones bancarias de movimientos, esto es que cualquier egreso tenga su debido soporte por conceptos como el pago de una factura, nmina, los desembolsos por caja menor debidamente soportados, etc.
          .tarjeta                   
            .row.row.justify-content-center.align-items-center
              .col-lg-10.slier-color.px-5.py-4
                h5.my-2 Cuenta por cobrar
                p Validar las cuentas por cobrar a clientes, es decir que las ventas pendientes de cobro evidencien la efectiva transaccin y el reconocimiento del cliente de recibido el producto, que los conceptos correspondan a los productos registrados en el sistema, que se haya liquidado el IVA pertinente de darse lugar, que no se haya cobrado dos veces, o que estn remisiones de mercancas pendientes de facturar.
          .tarjeta                 
            .row.row.justify-content-center.align-items-center
              .col-lg-10.slier-color.px-5.py-4
                h5.my-2 Validar cuentas
                p As mismo, se revisa cada una de las cuentas contables verificando la idoneidad de los soportes a los registros y su correcta contabilizacin. De otro lado, se verifica el procedimiento que se lleva para el registro de la informacin con fines que pueda ser optimizado y la informacin se garantice en fiabilidad y oportunidad mitigando riesgos por omisin.
          .tarjeta                  
            .row.row.justify-content-center.align-items-center
              .col-lg-10.slier-color.px-5.py-4
                h5.my-2 Consolidacin estados financieros
                p Para consolidar los estados financieros de propsito general es indispensable realizar el cierre contable, lo que tiene como finalidad reflejar las cuentas con los saldos en valores correctos, donde en caso de que se presenten diferencias o se identifiquen omisiones se deben realizar las respectivas correcciones y asientos de ajuste.                                
    .row.justify-content-center.align-items-center.mt-5          
