    #t_1_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.3.  Deterioro 
    p(data-aos="zoom-in") El concepto de deterioro refiere a la prdida de valor que sufren los activos cuando el valor registrado en libros es mayor al valor recuperable; es decir, al valor que se recibira si pretendiera vender o al momento de realizar una valoracin comercial de dicho activo, menos los costos y uso del bien.

    .row.justify-content-center.align-items-center.mt-5           
      .col-lg-6.col-color-2
        p.text-center.mb-0.p-2(data-aos="zoom-in") Veamos el deterioro contable de acuerdo a la normativa y sus excepciones:

    figure.mt-5
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/QeUCPYOmJtQ" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

    p(data-aos="zoom-in").mt-5 El deterioro del valor de los activos no corrientes excepto los indicados, depende de variables y factores del entorno, las cuales son externas a la empresa, no pueden ser controlados tal como es el caso de obsolescencia tecnolgica, la presencia comercial del activo, la sustitucin tecnolgica en el mercado, puede que sea nuevo el activo, pero una nueva tcnica hace que los activos caigan de valor sustantivamente, entre otros. 

