      h5 Figura 4.  
      span Obsolencia tecnolgica
    img.img-t.img-a.mt-3(data-aos="zoom-in-down")(src='@/assets/ilustraciones/img13.svg', alt='') 
    .row.justify-content-center.align-items-center.t1-color-row-1.p-5           
      .col-lg-12
        p(data-aos="zoom-in") Siendo esta la razn por la que al final del periodo se debe comparar el valor contable en libros de los activos con el valor real o comercial por medio de estudios de valoraciones que se debe ir llevando, y de darse se aplicar la correccin de su valor considerados en la NIC 36. 

        p.mb-0(data-aos="zoom-in") La NIC 2 es el marco normativo internacional que se aplica a todos los inventarios; excepto el de inversiones e instrumentos financieros, como los inventarios de activos agrcolas y biolgicos. La norma define los criterios de reconocimiento, medicin y presentacin de los inventarios en realizables o para la venta, en proceso y materiales; permitiendo identificar el conjunto de costos base a los recursos o elementos que los compongan. As mismo la norma, presenta las bases para determinar y medir el deterioro de este tipo de activos. 
    img.img-t.img-a.mt-5(data-aos="zoom-in-down")(src='@/assets/ilustraciones/img14.jpg', alt='')
    .row.justify-content-center.align-items-center.mt-5           
      .col-lg-8
        p(data-aos="zoom-in") Ahora se realiza un ejemplo de clculo mediante la comparacin y diferencia entre el valor en libros y valor a vender; es decir al precio en que se pueden obtener ingresos por la venta de dichos activos. Si el valor en libros es mayor al valor de venta realizable se considera que existe deterioro en los activos de inventarios; y se debe registrar la prdida como un gasto del periodo:  

    .row.justify-content-center.align-items-center.mt-5           
      .col-lg-10 
        img.img-t.img-a(data-aos="zoom-in-down")(src='@/assets/ilustraciones/img15.svg', alt='') 
    .row.justify-content-center.align-items-center.mt-5           
      .col-lg-8
        h5.text-center.color-purp(data-aos="zoom-in") Ejemplo de clculo del deterioro en inventarios: 
        p(data-aos="zoom-in").mt-5 La compaa Andariegos adquiere un lote de zapatos para comercializar, la compra la conforman 100 pares de zapatos a razn de $25.000 por par. El servicio de transportadora y entrega tuvo un precio de $100.000 hasta la bodega o local de venta. Este tipo de productos estn gravados por IVA y la empresa es responsable de este impuesto, por lo cual debe calcularse el IVA a tarifa convencional del 19%, la retencin en la fuente es de 2,5%, y la retencin por ICA 11,04 por 1.000.                        
