    #t_1_4.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.4.  Depreciaciones
    p(data-aos="zoom-in") Los desgastes que sufren los activos fijos por su uso en la operacin de la empresa se denominan depreciacin, este se debe calcular y reconocer como un gasto al dbito, lo que se lleva en el estado de resultados del periodo, afectando negativamente la generacin de utilidades de dicho periodo, as mismo, se debe llevar al activo como un menor valor en el crdito o haber, lo que disminuye el valor de los activos.

    p(data-aos="zoom-in") De acuerdo con el estndar internacional, la depreciacin se debe calcular desde el momento en que entra en uso, teniendo en cuenta el valor inicial en libros, la vida til del activo y lo establecido en las normas y polticas contables. La determinacin de la vida til de un activo fijo, es el periodo para el cual la empresa estima darle uso en la operacin de la empresa.

    .bloque-texto-g-1.color-secundario.p-5.p-sm-5.p-md-5p(data-aos="flip-down").mt-5
      .bloque-texto-g-1__img(
        :style="{'background-image': `url(${require('@/assets/ilustraciones/img17.jpg')})`}"
      )
      .bloque-texto-g-1__texto.p-4
        p.color-purp #[b Problemtica de anlisis]<br> 
        p La empresa Containers Ltda., adquiere un vehculo para uso de la empresa, en las polticas contables se estableci que la vida til de los vehculos ser de 3 aos, teniendo, ya que consideran que deben contar con una flota actualizada de vehculos, esto es que cada 3 aos venden y compran un vehculo, lo que define que ese ser el periodo de depreciacin en la empresa para los vehculos.<br>
        p Ahora validemos el anlisis de acuerdo a la normativa, el clculo y la vida til:  

    TabSpecial.color-acento-botones.mt-5
      .py-3.py-md-4(titulo="Anlisis" :imagen="images[0]" :numero="numbers[0]")

        h5.color-purp(data-aos="zoom-in") Anlisis de acuerdo con la normativa. 

        .row.justify-content-start.align-items-center.mt-5
          .col-lg-8.text-sup(data-aos="fade-right")
            p Se debe tener en cuenta al igual el desgaste del activo fijo y su obsolescencia tecno-mecnica como comercial, si por efectos de modificar esta poltica en la empresa para la sustitucin de los activos, esos cambios se debern reflejar como un cambio con una nota contable, en conformidad a lo establecido en la NIC 8 Polticas contables, cambios en las estimaciones contables y errores.

            p La NIC 16, en el prrafo 6 indica: El valor residual de un activo es el valor estimado que la empresa puede obtener de un activo por su venta o enajenacin, despus de haber deducido todos los costos y gastos para la venta, y si tuviera funcionalidad y la edad, y condiciones funcionales para operacin como un recurso til, al final de su vida til definida (NIC 16, p. 2). El valor residual, es un concepto utilizado comnmente en clculo de la depreciacin de activos fijos, pues es el valor referencial de base para la venta de dichos activos, esperando que al momento de venderlos sea superior generando ingresos adicionales que no son de la operacin, pero que favorecen las utilidades de la empresa.
        .row.justify-content-end.align-items-center.mt-4           
          .col-lg-5.d-none.d-lg-block(data-aos="fade-left")
            img.img-t.img-a.img-top(src='@/assets/ilustraciones/img18.svg', alt='')

        p(data-aos="fade-right") La NIC 16 expresa que se depreciar de forma separada cada activo o inclusive cada parte de un elemento de activos como propiedades, vehculos, planta y equipo, etc., que tengan un costo significativo con relacin al costo total, y esta depreciacin ser distribuida a cada parte o componente (NIC16, prrafos 43 y 44). En el caso de una empresa de aviacin se puede depreciar de manera separada la estructura de los aviones, y por otra parte los motores o turbinas.
        
      .py-3.py-md-4(titulo="Representacin" :imagen="images[1]" :numero="numbers[1]")
        h5.color-purp(data-aos="zoom-in") Representacin del clculo
        p(data-aos="fade-right").mt-5 La depreciacin de los activos fijos se detiene una vez se haya consumido el bien en el periodo establecido o se haya cumplido su periodo de vida til, nunca se podr aplicar depreciacin cuando los activos no se estn utilizando en la operacin o se den, de baja para la venta.

        p(data-aos="fade-right").mt-5 Para calcular la depreciacin contable, se parte de los siguientes aspectos constitutivos de su valor, y que son parte del valor en libros del activo.

        .row.justify-content-center.align-items-center.mt-5    
          .col-lg-10.bg-img-2.p-2(data-aos="fade-right")
