                p(data-aos="fade-right").text-center 
                  span(style='color:#E85F5B') #[b +] 
                  span(style='color:#00223D')  Gastos de aranceles, impuestos de nacionalizacin y dems aspectos no recuperables 
                p(data-aos="fade-right").text-center 
