            h5.color-purp(data-aos="zoom-in")   Mtodo de depreciacin
            p(data-aos="fade-right") La depreciacin se calcula sobre la sumatoria del precio de adquisicin, el impuesto sobre las ventas y costos y gastos para poner en operacin y funcionamiento los activos. Existen varios mtodos o formas para el clculo de la depreciacin como el de lnea recta, depreciacin acelerada, depreciacin en funcin al desgaste por unidades producidas, etc. Cualquiera es optativa para la empresa, pero se debe aplicar un solo criterio. Quizs el mtodo ms utilizado, equitativo y fcil de utilizar en las empresas es el de clculo por lnea recta el cual consiste en dividir el costo total del activo, entre el nmero de periodos de la vida til estimada segn cada tipo o conjunto de activos; dicho clculo en periodos hace que el valor a depreciar ser el mismo todos los periodos se expone.

            p(data-aos="fade-right") A continuacin, a travs de dos ejemplos verifiquemos la aplicacin de depreciacin en lnea recta y determinacin de deterioro:
            img.img-t.img-a.mt-5(data-aos="zoom-in-down")(src='@/assets/ilustraciones/img22.svg', alt='')

