                p(data-aos="fade-right") Una vez recibido el activo, se procede a realizar pruebas de funcionamiento del activo y otras adecuaciones y se paga $300.000 a un contratista. El vendedor de la embarcacin transfiere los riesgos y beneficios de la embarcacin el 7 de noviembre de 2020, sin embargo, el vehculo est listo para ser usado, el 12 de noviembre de 2020. Se debe calcular la depreciacin del mes de noviembre y diciembre.              
