    #t_1_5.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.5.  Amortizaciones  
    p(data-aos="zoom-in") Otro aspecto a verificar del ejercicio contable del periodo es lo concerniente a las amortizaciones, cuyo concepto consiste en repartir o diferir en cuotas los gastos pagados con anticipacin y van siendo consumidos a lo largo del periodo contable, y que cumplen con las definiciones de los activos, porque si son gastos de otro tipo se deben llevar y registrar en el gasto en el estado de resultados. Por lo anterior, para que un activo sea considerado para ser consumido por cuotas debe cumplir con las siguientes caractersticas: 
    .row.justify-content-center.align-items-center
      .col-lg-8
        .tarjeta.tarjeta--azul.slider-color.py-4.px-0.br-radius.mt-5
          SlyderA
