    #t_1_6.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.6.  Diferencia en cambio de visas 
    p(data-aos="zoom-in") Dentro del ejercicio empresarial este tipo de revisiones y verificaciones son muy importantes, porque afectan el efectivo y disponible, y se dan cuando la empresa realiza transacciones comerciales en moneda extranjera regularmente en dlares o euros que son las ms utilizadas, ya que tienen contratos o celebran negocios en el extranjero; por lo tanto, al realizar la conversin de moneda para la presentacin de la informacin contable la empresa se encuentra expuesta al riesgo cambiario que puede ser sancionable, y genera diferencias que deben corregirse y registrarse, su ajuste consiste en reflejar la realidad del valor de las transacciones y del impacto econmico en la empresa sobre el efectivo de bancos y sus costos de cambio en s.

    .row.justify-content-center.align-items-center.mt-5  
      .col-lg-6
        p(data-aos="zoom-in") La Norma Internacional de contabilidad NIC- 21 (p A195), expresa en su objetivo:
        .hr
    .row.justify-content-center.align-items-stretch.mt-5  
      .col-lg-5.color-col-1.p-4(data-aos="fade-right")
        img.img-a.img-t.my-3(src='@/assets/ilustraciones/img29.svg', alt='')
        p(data-aos="zoom-in") 1. Una empresa puede llevar a cabo actividades en el extranjero de dos maneras diferentes. Puede realizar transacciones en moneda extranjera o bien puede tener negocios en el extranjero. Adems, la entidad puede presentar sus estados financieros en una moneda extranjera. El objetivo de esta norma es indicar cmo se incorporan, en los estados financieros de una entidad, las transacciones en moneda extranjera y los negocios en el extranjero, y cmo convertir los estados financieros a la moneda de la locacin donde opera.  

      .col-lg-5.color-col-2.p-4(data-aos="fade-left")
        img.img-a.img-t.my-3(src='@/assets/ilustraciones/img30.svg', alt='')
        p(data-aos="zoom-in") 2. Los principales problemas que se presentan son la tasa o tasas de cambio a utilizar, as como la manera de informar sobre los efectos de las variaciones en las tasas de cambio dentro de los estados financieros.

    .row.justify-content-center.align-items-center.mt-5  
      .col-lg-10
        p(data-aos="zoom-in").text-center Cabe resaltar que para el grupo 2- NIIF pymes aplica la seccin 30 Conversin de la moneda extranjera. 
        p.color-purp(data-aos="zoom-in-down") Ejemplo de verificacin y ajuste por tipo de cambio: 
        p(data-aos="zoom-in") El 24 de noviembre de 2020 la empresa Container Ltda. adquiere maquinaria y equipo por valor de USD$57.000, a una tasa de COP$3.500. El 18 de diciembre de 2020, se transfiere el riesgo una vez la mquina se encuentra en Colombia, fecha en la cual se realizar el pago. Cuando se realiza el proceso de nacionalizacin la tasa vigente es COP$3.620.
        p(data-aos="zoom-in") De acuerdo con lo anterior se tiene:
    .row.justify-content-center.align-items-center.mt-5  
      .col-lg-4
        p(data-aos="zoom-in").text-center.p-3.mb-0.p-color-4 24 de noviembre de 2020. 
    .row.justify-content-center.align-items-center.mt-5  
