        p(data-aos="zoom-in") Se origina una diferencia por diferencia en cambio, aumenta el valor del pasivo, ya que subi la tasa de cambio el da en que se dio la llegada y entrega de la mquina en el puerto de Colombia, el valor de la factura es el mismo ante el proveedor, pero esta obligacin en pesos es mayor, como consecuencia del incremento en la tasa de cambio del da, que es donde ocurre el hecho econmico de entrega oficialmente. El nuevo saldo de la obligacin es de $206.340.000, lo que implic un sobre costo de $6.840.000, al momento de reconocer la entrega y recibo de la mquina, y ser el dato que se debe verificar haya sido registrado en la contabilidad. La Norma internacional de Informacin Financiera para las pymes, indica que el ajuste por diferencia en cambio monetario debe incluirse en los resultados del periodo y no debe incluirse como parte del costo del activo en su valor general. 
