    #t_1_7.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.7.  Arqueo de caja
    p(data-aos="zoom-in") Las empresas hacen el manejo del efectivo lquido o eminentemente monetario, en las cuentas que dispone la contabilidad las cuales son la de caja general y caja menor, donde esta ltima se utiliza especialmente para compras y gastos de menor cuanta, la cual en su globalidad se debe establecer por polticas contables de la empresa, as como el valor del fondo de dicha caja y los conceptos de recursos en los que se pueden aplicar dichos fondos. En segunda instancia, la caja general se utiliza para cancelar gastos por conceptos de mayor cuanta. En los dos casos es perentorio realizar la verificacin y realizar el arqueo a cada caja, operacin que consiste en comparar el valor de libros y sus soportes con los dineros suministrados y que sumen lo que efectivamente existe en la caja ms el disponible que den como resultado el valor inicial del fondo.

    p(data-aos="zoom-in") Esta operacin de arqueo por comparacin puede identificar diferencias que requieren de ajustes contables que mediante una nota de contabilidad se pueden cerrar dichas brechas, dentro de las que se pueden encontrar:

    .row.justify-content-center.align-items-center.mt-5  
      .col-lg-10.caja-color
        .row.justify-content-center.align-items-center  
          .col-lg-4
            img.img-a.img-t.my-3(src='@/assets/ilustraciones/img31.svg', alt='')
          .col-lg-8
            p(data-aos="zoom-in").mb-0 #[b El valor en libros es mayor al valor encontrado al momento del arqueo de caja]: esto representa un faltante de dinero, por ende, se debe identificar si falto algo por registrar o identificar al responsable de dicha situacin para que reporte las transacciones faltantes y proceder a realizar el ajuste con el fin de reflejar la realidad econmica y el saldo correcto del efectivo. 

    p(data-aos="zoom-in").mt-5.text-center Enseguida se pueden detallar algunos ejemplos de caja menor segn los valores encontrados:
    
    AcordionA(data-aos="zoom-in-down").mt-5(tipo="b" clase-tarjeta="tarjeta tarjeta--azul")
