              p.mt-5 Faltante injustificado, no se evidencia el concepto o soporte por el que falta dinero en la caja, una vez se realiz el arqueo. Por lo anterior, el colaborador es responsable de este suceso y deber sustentar y/o responder.

      .row.justify-content-center.align-items-center.title-color(titulo=" B. Ej.: Faltante caja menor ")
        .col-lg-11
          p Se realiz un arqueo de caja, y se observa que se present un faltante injustificado y sin soporte del gasto por valor de $ 50.000.
