              p.mt-5 El segundo caso de diferencia en caja obedece cuando el valor de libros es menor al valor del dinero encontrado al momento del arqueo de la caja, en este caso, la diferencia representa un sobrante de dinero, ya que hay ms soportes que en sumatoria junto con el dinero efectivo, superan el valor del fondo, lo cual tambin exige realizar un ajuste contable. Los casos que se presentan son los siguientes:

      .row.justify-content-center.align-items-center.title-color(titulo=" C.	Ej.: Excedente caja menor")
        .col-lg-11
          p Una cliente abona a sus cuentas pendientes un valor de $500.000, pagando una de sus facturas pendientes, el valor es recibido en la ventanilla de la empresa, sin embargo, no se realiza el registro contable; por lo tanto, se debe adelantar un ajuste aumentando la cuenta de efectivo.
