              p.mt-5 Cuando los sobrantes en la caja injustificados son de cuanta menor se aumenta el valor de efectivo y el valor de los ingresos, pero si es un valor mayor y significativo, debe establecerse las posibles causas que originaron el sobrante y se llevara esa partida registrndola como un pasivo, una obligacin frente a acreedores varios, mientras se reconoce su procedencia de excedente.

