    #t_1_7.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.8.  Conciliacin bancaria
    p(data-aos="zoom-in") Este proceso es semejante y corresponde analgicamente a lo que se realiza en la operacin de arqueo de la caja, pero en este caso se hace sobre los recursos monetarios manejados en cuentas bancarias, bien sea de cuentas ahorro o cuentas corrientes regularmente. La conciliacin bancaria es el proceso de verificacin y comparacin de los valores registrados en libros de cada una de las cuentas de bancos, en contraste con los documentos externos de extracto de la cuenta de ahorros o corriente que es generado y reportado por la entidad financiera donde la empresa tiene la cuenta; el objetivo de este ejercicio, como se mencion, es identificar las partidas de movimientos de la cuenta y sus diferencias para, posteriormente, realizar los ajustes contables respectivos, que permitan llevar la informacin de manera entendible, sustentada de cada movimiento, til y oportuna para la toma de decisiones.

    .row.justify-content-center.align-items-center.mt-5      
      .col-lg-10.cajon.color-primario.py-4.mb-4.px-5(data-aos="fade-left")
        p.mb-0 Regularmente el ejercicio de conciliacin se recomienda hacerse de forma mensual, dado su volumen, y teniendo en cuenta confirmar las fechas de las transacciones, los montos en valor, y los soportes de las transacciones en caso de compras o pagos. Posteriormente, se realiza un anlisis de las diferencias hallazgos encontrados, procediendo a realizar los ajustes.

    .row.justify-content-center.align-items-center.mt-4      
      .col-lg-10
        p Los resultados o hallazgos en las verificaciones realizadas por conciliacin bancaria, que con mayor frecuencia se presentan son los siguientes:
        .hr-gray.my-3
    .row.justify-content-center.align-items-center
      .col-lg-1
        img.img-a.img-t.my-3(src='@/assets/ilustraciones/a.svg', alt='')
      .col-lg-9
        p.color-purp(data-aos="zoom-in") #[b Valores no registrados por la empresa y que se reflejan en el extracto bancario.] 
        p.mb-0(data-aos="fade-left") En este caso se debe realizar ajustes mediante notas dbito y notas crdito, de acuerdo con la transaccin que se est presentando.
    .row.justify-content-center.align-items-center
      .col-lg-10    
        .hr-gray.my-3
    .row.justify-content-center.align-items-start
      .col-lg-1
        img.img-a.img-t.my-3(src='@/assets/ilustraciones/b.svg', alt='')
      .col-lg-9
        p.color-purp(data-aos="zoom-in") #[b Otros valores no registrados.] 
        p(data-aos="fade-left") Dentro de estos hallazgos que se presentan en la conciliacin bancaria se encuentran los gastos por concepto de chequeras, comisiones bancarias cobradas, comisiones cobradas por cheques devueltos, intereses de sobregiros, intereses por prstamos y otros cobros realizados por las entidades financieras; los ajustes en estos casos disminuyen la cuenta de bancos en el crdito (haber) y aumentan la cuenta al dbito (debe), para los cual se usan las notas contables, relacionada con el concepto que se est cancelando. Ahora, se revisarn algunos ejemplos:

        p.color-purp(data-aos="fade-right").color-gray.mt-5 #[b 1)   Ej.: Registro por compra chequera]
        p(data-aos="zoom-in") La empresa Containers Ltda., no registra la compra de chequera por valor de $180.000, ms IVA del 19%, la cual se encuentra debidamente registrada en el extracto bancario emitido por la entidad financiera, y es con la que opera la empresa. 

        .row.justify-content-center.align-items-center
          .col-lg-10
            .titulo-sexto.color-acento-contenido.mt-5
              h5 Tabla 22.  
              span Ajuste de Asiento por compra de chequera
