                  tr
                    td IVA
                    td.text-center $ 34.200         
                    td.text-center
                  tr
                    td Bancos
                    td.text-center 
                    td.text-center $ 214.200                     
                  tr
                    td Total
                    td.text-center $ 214.200
                    td.text-center $ 214.200

